import React from 'react';
import Button from 'react-bootstrap/Button';
import Dropdown from 'react-bootstrap/Dropdown';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import './Display.css'

export default class RX extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            device: props.device
        };
    }
    componentWillReceiveProps({device}) {
        this.setState({...this.state,device})
      }
    populateChannels () {
        let items = []
        for(let i = 0; i < 38; i++){
            let h = "#/action-"+ i
            items.push(<Dropdown.Item href={ h } >{ i }</Dropdown.Item>)
        }
        return items
    }
    
    RXRender () {
        console.log(this.state.device)
        if (this.state.device !== null){
            let device_name = this.state.device.device.name

            return (
                <div>
                    <br />
                    <div>Connected to { device_name }</div> 
                    <br />
                    <Accordion className='Accordian'>
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Click to configure reciever
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                <Dropdown as={ButtonGroup}>
                                    <Button variant="success" style={{pointerEvents: 'none'}}>Channel</Button>
                                    <Dropdown.Toggle split variant="success" id="dropdown-split-basic" />
                                    <Dropdown.Menu>
                                        { this.populateChannels() }
                                    </Dropdown.Menu>
                                </Dropdown>{' '}
                                <div style={{float:'right'}}>Here we are</div>
                                <br />
                                <Dropdown as={ButtonGroup}>
                                    <Button variant="success" style={{pointerEvents: 'none'}} >PHY</Button>
                                    <Dropdown.Toggle split variant="success" id="dropdown-split-basic" />
                                    <Dropdown.Menu>
                                        <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
                                        <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
                                        <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
                                    </Dropdown.Menu>
                                    </Dropdown>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion className='Accordian' style={{float:'right'}, {width:'360px'}}>
                        <Card style={{width:'360px'}}>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Click to configure transmitter
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                <Dropdown as={ButtonGroup}>
                                    <Button variant="success" style={{pointerEvents: 'none'}}>Channel</Button>
                                    <Dropdown.Toggle split variant="success" id="dropdown-split-basic" />
                                    <Dropdown.Menu>
                                        { this.populateChannels() }
                                    </Dropdown.Menu>
                                </Dropdown>{' '}
                                <div style={{float:'right'}}>Here we are</div>
                                <br />
                                <Dropdown as={ButtonGroup}>
                                    <Button variant="success" style={{pointerEvents: 'none'}} >PHY</Button>
                                    <Dropdown.Toggle split variant="success" id="dropdown-split-basic" />
                                    <Dropdown.Menu>
                                        <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
                                        <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
                                        <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
                                    </Dropdown.Menu>
                                    </Dropdown>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <br /> <br />
                    <div>
                        <Button variant='primary'>
                            Send Message
                        </Button>{' '}
                        <Button variant='primary'>
                            Record data
                        </Button>
                    </div>
                </div>
            );
        }
        else {
            return <div></div>
        }
    }

    render() {
        console.log("WERE IN HERE44")
        return (
            this.RXRender()
        );
    }
}
