import React from 'react';
import Button from 'react-bootstrap/Button';
import RX from './RX';

export default class Bluetooth extends React.Component{
    constructor(props) {
        super(props);
        let node = {
            device:{
                name:"test"
            }
        }
        this.state = {
            rx_device: null
        };
    }
    handleClick(){
        console.log('Requesting any Bluetooth Device...');
        let options = {
            acceptAllDevices: true
        };
        return navigator.bluetooth.requestDevice(options)
            .then(device => device.gatt.connect())
            .then(device => this.setState({rx_device: device }))
    }
    checkConnected() {
        if (this.state.device == null)
            return(
                    <div>

                        <p>Click to connect your bluetooth device to the browser</p>
                        <Button variant="primary" onClick={() => this.handleClick()}>
                            Connect
                        </Button>{' '}
                    </div>
            )
        else
            return (
                <div>
                    <RX device={this.state.rx_device} />
                </div>
            )
    }

    render () {
        console.log('HERWE any Bluetooth Device...');
        return (
            this.checkConnected()
        );
    }
}